@import '../styles/shared.scss';

.section-info {
    padding-top: 59px;
    padding-bottom: 87px;
}

.info__flex-box {
    display: flex;
    flex-wrap: wrap;
    margin-left: 96px;
    margin-bottom: 45px;
    position: relative;

    &::before {
        position: absolute;
        content: '';
        display: block;
        background: #864d3f;
        width: 100%;
        height: 3px;
        top: -29px;
    }

    &::after {
        content: '';
        display: block;
        height: 3px;
        width: 100%;
        background: #864d3f;
        position: absolute;
        bottom: -17px;
        left: 0;
    }
}

.info__general {
    width: 469px;
    margin-right: 138px;
    position: relative;

    // &::before {
    //     position: absolute;
    //     content: '';
    //     display: block;
    //     background: #864d3f;
    //     width: 100%;
    //     height: 3px;
    //     top: -29px;
    // }

    // &::after {
    //     content: '';
    //     display: block;
    //     height: 3px;
    //     width: 100%;
    //     background: #864d3f;
    //     position: absolute;
    //     bottom: 0;
    //     left: 0;
    // }

    .info__content {
        &:not(:last-child) {
            margin-bottom: 45px;
        }
    }

}

.info__special {
    width: 547px;

    .info__content {
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
}

.title {
    @extend %title;

    margin-bottom: 20px;
    padding: 10px;
    text-align: center;

    font-size: 36px;
    line-height: 41px;
    background: rgba(230, 219, 208, 0.72);
    border-radius: 20px;
}

.text {
    font-family: "Open Sans", sans-serif;

    font-size: 24px;
    line-height: 1.66;
    color: $secondary-text;

    &.projects__text {
        line-height: 1.55;
    }
}

.projects__item {
    &:not(:last-child) {
        margin-bottom: 14px;
    }

    .projects__title {

        margin-bottom: 8px;
        font-size: 26px;

    }
}

.tech-skills__item {
    padding-left: 30px;
    position: relative;

    &:not(:last-child) {
        margin-bottom: 5px;
    }

    &.text {
        text-transform: uppercase;
        line-height: 1.55;
    }

    &::before {
        content: '';
        display: block;
        position: absolute;
        background-image: url('./radio-checked.png');
        width: 13px;
        height: 13px;
        background-size: contain;
        background-repeat: no-repeat;
        top: 10px;
        left: 0px;
    }
}

.additional-container {
    display: flex;
    margin-left: 96px;
}

// .additional-box {
//     margin-left: 96px;
// }

.work-experience,
.education {
    font-family: 'Open Sans',
        sans-serif;
    font-size: 20px;
    line-height: 1.2;
}

.work-experience {
    width: 469px;
    margin-right: 138px;

    .date-period {
        margin-bottom: 15px;
    }
}

.work-experience__responsibilities-list {
    padding-left: 20px;
    list-style: circle;
}

.work-experience__item {
    &:not(:last-child) {
        margin-bottom: 20px;
    }
}

.university__title {
    margin-bottom: 10px;

    font-size: 24px;
    line-height: 1.52;
    color: $primary-text;

}

.profession__title {
    margin-bottom: 10px;
    color: $secondary-text;
}

.date-period {
    font-style: italic;

    font-size: 20px;

    color: rgba(172, 166, 166, 0.851);
}

.courses {
    width: 547px;
    font-size: 20px;
    line-height: 1.32;
    font-family: Open Sans;
}

.courses-list__item {
    &:not(:last-child) {
        margin-bottom: 20px;
    }
}

.courses__title {

    color: $primary-text;
    margin-bottom: 10px;
    font-size: 20px;

    .courses__name {
        color: $secondary-text;
    }
}